.music li.musiclist-item {
  display: flex;
  align-items: center;
}
.music li.musiclist-item .index {
  margin-left: 10px;
}
.music li.musiclist-item:nth-child(1),
.music li.musiclist-item:nth-child(2),
.music li.musiclist-item:nth-child(3) {
  padding: 4px 0;
}
.music li.musiclist-item:nth-child(1) .index,
.music li.musiclist-item:nth-child(2) .index,
.music li.musiclist-item:nth-child(3) .index {
  color: red;
  font-size: 17px;
}
.music li.musiclist-item .info {
  flex: 1;
  display: flex;
  align-items: center;
  border-bottom: 1px solid lightgray;
  padding: 10px 0;
  padding-right: 10px;
  margin-left: 10px;
}
.music li.musiclist-item .info .detail {
  flex: 1;
}
.music li.musiclist-item .info .detail p {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 12px;
}
.music li.musiclist-item .info .detail p .artist::after {
  content: "/";
}
.music li.musiclist-item .info .detail p span:last-of-type::after {
  content: "";
}
.music li.musiclist-item .icon {
  width: 22px;
  height: 22px;
  background-image: url("../assets/index_icon_2x.png");
  background-size: 166px 97px;
  background-position: -24px 0;
}
