























.talk{
  width: 100%;
  float: left;

  .content{
    position: relative;
    .top{
      display: flex;
      justify-content: space-between;
      img{
      display: block;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      position: absolute;
      top: 11px;
      left: 10px;
    }
    }

    h1{
      display: inline-block;
      color: gray;
      font-size: 16px;
      margin-left: 60px;
      margin-top: 18px;
     
    }
    span{
      display: block;
      font-size: 16px;
      margin: 5px 5px;
      padding-bottom: 15px;
      padding-left: 15px;
     // border-bottom: 1px solid rgba(204, 204, 204, 0.397);
      text-indent: 2em;
    }
  .footer{
          border-bottom: 1px solid rgba(204, 204, 204, 0.397);
          margin-bottom: 15px;
  }
  }
}
