
























































.control {
  // position: absolute;
  // bottom: 0;
  // left: 0;
  // width: 100%;
  .progerss {
    width: 100%;
    height: 10px;
    // background: lightcoral;

    position: relative;
    display: flex;
    align-items: center;

    input {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      z-index: 1;
      opacity: 0;
    }
    .line {
      background: white;
      width: 100%;
      height: 4px;
      border-radius: 10px;
    }
    .dot {
      width: 10px;
      height: 10px;
      background: white;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      margin-left: -5px;
      z-index: 0;
      box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
    }
  }
  .icons {
    margin:50px 0;
    display: flex;
    justify-content: space-around;
  }
}
