












































































.playlist {
  position: relative;
  
  .head-img {
    display: flex;
    width: 100%;
    height: 210px;
    padding-bottom: 15px;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 210px;
      background: rgba(0, 0, 0, 0.6);
      position: absolute;
      top: 0;
       
    }

    .leftpic {
      img {
        width: 120px;
        height: 120px;
        margin: 30px;
        position: relative;
        z-index: 1;
      }
      .playcount {
        position: absolute;
        top: 36px;
        left: 90px;
        font-size: 10px;
        color: gray;
      }
    }
    .rightname {
      font-size: 12px;
     position: absolute;
      top: 160px;
      right: 60px;
      color: white;
      z-index: 1;
    }
  }
}
