li.musiclist-item {
  display: flex;
  // background: lightblue;
  // margin: 10px  10px;

  align-items: center;

  .index {
    margin-left: 10px;
    // font-size: 1.2em;
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    padding: 4px 0;
    .index {
      color: red;
      font-size: 17px;
    }
  }

  .info {
    flex: 1;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
    padding: 10px 0;
    padding-right: 10px;
    margin-left: 10px;
    .detail {
      flex: 1;
      h3 {
      }
      p {
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        font-size: 12px;
        .artist {
          &::after {
            content: "/";
          }
        }
        span {
          &:last-of-type {
            // background: red;
            &::after {
              content: "";
            }
          }
        }
        i {
          // white-space: nowrap;
        }
      }
    }
  }

  .icon {
    width: 22px;
    height: 22px;
    background-image: url("../assets/index_icon_2x.png");
    background-size: 166px 97px;
    background-position: -24px 0;
  }
}
}
