.playbar {
  width: 100%;
  height: 60px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  padding: 5px 10px;
  align-items: center;
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.15);
}
.playbar .radius {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}
.playbar h5 {
  flex: 1;
  padding: 0 10px;
}
.playbar .progress {
  position: relative;
  width: 34px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  margin-right: 17px;
}
.playbar .progress span.play-icon {
  width: 15px;
  height: 15px;
  overflow: hidden;
}
.playbar .progress span.play-icon::before {
  content: "";
  width: 30px;
  height: 15px;
  display: block;
  border-top: 7.5px solid transparent;
  border-right: 18px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 12px solid #ccc;
  margin-left: 4px;
}
.playbar .progress span.paused-icon {
  overflow: hidden;
}
.playbar .progress .currentlist {
  width: 30px;
  height: 30px;
  margin-top: 10px;
}
.playbar .progress canvas {
  position: absolute;
  top: -2px;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
