




































li.current-play-list-item {
  display: flex;
    margin-left: 15px;
margin: 15px;
  @keyframes shensuo {
    from {
      height: 20%;
    }
    to {
      height: 100%;
    }
  }
  .palying-animate {
    width: 20px;
    height: 20px;
    // background: green;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
   
    span {
      width: 20%;
      height: 20%;
      background: red;
      animation: shensuo 0.6s linear infinite alternate;
      // margin-right: 8px;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.4s;
      }
    }
    &.paused {
      span {
        animation-play-state: paused;
      }
    }
  }
  .detail {
    flex: 1;
  margin-left: 10px;
  }
}
