#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#app .header-like {
  width: 100%;
  height: 84px;
  background: #d43c33;
  color: white;
  line-height: 84px;
}
#app .header-like h1 {
  margin-left: 45px;
  font-size: 24px;
  position: relative;
}
#app .header-like h1 img {
  position: absolute;
  top: 30px;
  left: -30px;
}
#app.showbar {
  margin-bottom: 50px;
}
#nav {
  display: flex;
  box-shadow: 0 -1px 0px 0px #7b7b7b5c inset;
}
#nav li {
  flex: 1;
  text-align: center;
}
#nav li a {
  color: #2c3e50;
  text-decoration: none;
  line-height: 40px;
  padding: 0 5px;
  display: inline-block;
  font-size: 14px;
}
#nav li a.router-link-exact-active {
  color: #dd001b;
  border-bottom: 2px solid #dd001b;
}
